.first
{
    min-height: 640px;
    background-image: url("../img/fon2.jpg");
    background-position: left;
    background-repeat: no-repeat;
    background-size: contain;
    padding: 163px 0 60px 548px;
}

.first__heading
{
    font-size: 124px;
    text-transform: uppercase;
    font-family: "Baskerville";
    width: 590px;
    line-height: 127px;
    letter-spacing: 9px;
}

.page-link
{
    color: #801177;
    background-color: transparent; 
    border: none;
}

.bottom
{
    position: fixed;
    left: 0;
    bottom: 0;
    right: 0;
    min-height: 67px;
    background-color: rgba(255, 243, 147, .7);
}

.bottom__container
{
    display: flex;
    justify-content: space-between;
    align-items: baseline;

    max-width: 1100px;

}

.fab 
{
    font-size: 24px;
}

.page-link:hover
{
    background-color: transparent;
    border-color: transparent;
}

.catalog,
.gifts,
.feedback
{
    padding: 50px 0 90px;
}

.card-group > .card
{
    flex: 1 0 33.33%;
}

.heading
{
    text-align: center;
    margin-bottom: 60px;
}

.btn-primary
{
    margin-top: 40px;
    background-color: #6d2377;
    border-color: #6d2377;
}

.carousel-indicators
{
    bottom: -50px;
}

.carousel-control-next, 
.carousel-control-prev
{
    width: 3%;
}

.carousel-control-next
{
    right: -35px;
} 

.carousel-control-prev
{
    left: -35px;
}

.carousel {
    min-height: 200px;
    display: flex;
    align-items: center;
}